'use strict';

define(['knockout', 'knockout-mapping'], function (ko, mapping) {

	"use strict";

	/**
  * Used for testing only, creates a console log of the element and the data used in the binding
  */

	ko.bindingHandlers.logger = {
		update: function update(element, valueAccessor, allBindings) {

			//store a counter with this element
			var count = ko.utils.domData.get(element, "_ko_logger") || 0,
			    data = ko.toJS(valueAccessor() || allBindings());

			ko.utils.domData.set(element, "_ko_logger", ++count);

			if (console && console.log) {
				console.log(count, element, data);
			}
		}
	};

	/**
  * Shows a JSON output of the bound object
  */
	ko.bindingHandlers.debug = {
		update: function update(element, valueAccessor) {

			var val = ko.unwrap(valueAccessor());

			element.innerText = JSON.stringify(mapping.toJS(val), null, 2);
			element.style.display = 'block';
			element.style.visibility = 'visible';
			element.className = element.className.replace(/hide|hidden/, '');
		}
	};
});